.FilterInput{
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.FilterInput input{
    height: 2rem;
    width: 20rem;

    border: none;
    outline: none;
    
    border-bottom:3px solid black ;
    font-size: 1.2rem;
}


.FilterInput input:focus{
   background-color: rgba(0, 0, 255, 0.1);
}



.FilterInput h1{
   display: inline-block;
}