.form{
width: 500px;

background-color: white;
box-shadow: 2px 2px 10px 2px rgba(128, 128, 128, 0.444);
padding: 1rem 2rem;

display: grid;
grid-auto-rows: 70px;

gap: 1rem;
text-align: left;

position: fixed;
top:15%;
left: 35%;
}

.form input {
height: 60%;
width: 100%;   

border: none;
outline: none;
font-size: 1.2rem;
}

#status input{
    height: auto;
    width: auto;
}
#button{
    display: flex;
    justify-content: end;
    align-items: center;
}

#button button {
font-size: 1rem;
transition: 0.3s;

margin: 0 1rem;
padding: 10px 30px ;

border: none;
border-radius: 10px;
}

#button button:hover {
    font-size: 1.1rem;
}
#button button:active {
    font-size: 1rem;

}


.form .validInput{
    background-color: rgba(0, 21, 255, 0.074);
    border-bottom:3px solid black;

} 
.form .InvalidInput{
    background-color: rgba(255, 0, 0, 0.074);
    border-bottom:3px solid rgb(192, 3, 3);

} 
.cancel{
    background-color: rgb(27, 27, 69);
    color: white;
}
.save{
    background-color: rgb(51, 138, 52);
    color: white;
}




