.Home{
    height: 100vh;
    padding-top: 2rem;
}
.addButton{

    background-color: rgb(7, 22, 89);  
    color: white;
    
    position: fixed;
    bottom: 30px;
    right: 10px;

    padding: 30px 20px;
    box-shadow: 2px 2px 3px 2px gray;
    border-radius: 50%;
    font-size: 1.5rem;

    border: none;
    transition: 0.4s;
    cursor: pointer;
}

.addButton:hover{
    font-size: 1.6rem;

}
.addButton:active{
    font-size: 1.5rem;
}

.text{
    font-size: 40px;
}
.Home h2{
    margin-top: 10rem;
}