.cart{
    width: 18rem;

    display: grid;
    gap: 2px;

    margin: 1rem;
    padding: 1rem;
    box-shadow: 2px 2px 10px 2px rgba(128, 128, 128, 0.396);
}
.cart span {
    font-size: 1rem;
} 
.cart h2{
    margin-top: 0;
    font-size: 1.1rem;

}


.user{
    height: 4rem;
    width: 4rem;

    margin: auto;
    border-radius: 50%;
    overflow: hidden;
}

.circle1{
    height: 2rem;
    width: 2rem;

    border: 3px solid black;
    margin: auto;
    border-radius: 50%;
}

.circle2{
    height: 4rem;
    width: 4rem;

    border: 3px solid black;
    border-radius: 50%;
    margin: auto;
}

.cart-container{
    display: flex;
    flex-wrap: wrap;
}
.cartButton{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
}
.cartButton button{
    font-size: 1rem;
    padding:0.3rem 0.5rem;

    border:none;
    border-radius: 10px;
    cursor: pointer;

    color:white;
}
.cartButton :nth-child(1){
    background-color: rgb(155, 10, 10);
}
.cartButton :nth-child(2){
    background-color: rgb(10, 154, 190);

}